const GET_QUESTIONS = gql`
query($id : Int !){
    bioYours(id: $id){
        id
        title
        brif
        is_Draft
        created_at
        updated_at
        questions
        { id    
          title
          maximum_time
          video{
            id
            url
            frame{
              url
            }
          }
        }
    }
  }
`
